.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}
.footer .heading{
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}
.footer .heading h1 {
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
    letter-spacing: -0.04rem;
    font-family: var(--font-family);
}
.footer .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid white;
    text-align: center;
    margin-bottom: 10rem;
    cursor: pointer;
}
.footer .btn p{
    box-sizing: border-box;
    font-size: 18px;
    line-height: 21px;
    color: white;
    font-family: var(--font-family);
    word-spacing: 2px;
}
.footer .links {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
    
}
.footer .links div {
    width: 250px;
    margin: 1rem;
}
.footer .links .logo {
    display: flex;
    flex-direction: column;
}
.footer .links .logo img {
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}
.footer .links .logo p{
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
}
.footer .links .linksDiv {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}
.footer .links .linksDiv h4 {
    font-family: var(--font-family);
    font-size: 18px;
    line-height: 16px;
    margin-bottom: 1rem;
    height: fit-content;
}
.footer .links .linksDiv p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;
    margin: 0.5rem 0;
    cursor: pointer;
}
.footer .copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}
.footer .copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    color: white;    
}
@media (max-width: 850px) {
    .footer .heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}
@media (max-width: 550px) {
    .footer .heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
    .footer .linksDiv {
        margin: 1rem 0;  
    }
    .footer .btn p {
        font-size: 14px;
        line-height: 20px;
    }
}