:root {
  --font-family: 'Manrope', sans-serif;
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.possibility {
    display: flex;
    flex-direction: row;
}
.possibility .image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-right: 2rem;
}
.possibility .image img {
    width: 100%;
    height: 100%;
}
.possibility .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
}
.possibility .content h4 {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;
    color: #71E5FF;
}
.possibility .content h4:last-child {
    color: #FF8A71;
}
.possibility .content h1 {
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
    font-family: var(--font-family);
    margin: 2rem 0;
}
.possibility .content p{
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);
    margin: 2rem 0;
}
@media (max-width: 950px) {
    .possibility {
        flex-direction: column;   
    }
    .possibility .image {
        margin: 1rem 0;
    }
    .possibility .content {
        margin-top: 2rem;
    }
}