.features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.features .heading {
    flex: 1;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}
.features .heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    font-weight: 800;
    line-height: 45px;
}
.features .heading p {
    font-family: var(--font-family);
    font-size: 16px;
    font-weight: 500;
    line-height: 30px;  
    color: #FF8A71;
    margin-top: 2rem;  
}
.features .container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}
@media (max-width: 990px) {
    .features {
        flex-direction: column;
    }
    .features .heading {
        margin: 0 0 2rem 0
    }
}
@media (max-width: 550px) {
    .features .heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
    .features .heading {
        margin: 0 0 2rem 0
    }
}