/* Global Rules */
:root {
  --font-family: 'Manrope', sans-serif;
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
body {
    background: var(--color-bg);
    font-family: var(--font-family);
}
a {
    color: unset;
    text-decoration: none;
}
.gradientBg {
    background-image: linear-gradient(to right, #002853, #032243, #061b34, #081526, #040c18);
}
.gradientText {
    background: #AE67FA;
    background: -webkit-linear-gradient(to right, #AE67FA 0%, #F49867 100%);
    background: -moz-linear-gradient(to right, #AE67FA 0%, #F49867 100%);
    background: linear-gradient(to right, #AE67FA 0%, #F49867 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.sectionPadding{
    padding: 4rem 6rem;
}
.sectionMargin{
    margin: 4rem 6rem;
}
/*End of Global Rules */
/* Animation */
.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}
@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
         transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
        transform: scale(1);
  }
}
@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
         transform: scale(0.5);
  }
  100% {
    -webkit-transform: scale(1);
        transform: scale(1);
  }
}
/*End Animation */
/* Media Queries */
@media screen and (max-width: 700px) {
    .sectionPadding {
        padding: 4rem;
    }
    .sectionMargin {
        margin: 4rem;
    }
}
@media screen and (max-width: 550px) {
    .sectionPadding {
        padding: 4rem 2rem;
    }
    .sectionMargin {
        margin: 4rem 2rem;
    }
}
/* End of Media Queries */