.brand {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.brand .logo {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    min-width: 120px;
    max-width: 150px;
    flex: 1;
    margin: 1rem;
}
.brand .logo div {
    margin: 5px;
}
@media (min-width: 769px) {
    .brand {
        display: grid;
        grid-template-columns: repeat(2,300px);
    }
}