.blog {
    display: flex;
    flex-direction: column;
}
.blog .heading {
    width: 100%;
    text-align: center;
    margin-bottom: 5rem;
}
.blog .heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    line-height: 75px;
    font-weight: 800;
    letter-spacing: -0.04rem;
}
.blog .container {
    display: flex;
}
.blog .container .groupA {
    flex: 0.75;
    margin-right: 2rem;
}
.blog .container .groupB {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
}
@media (max-width: 990px) {
    .blog .container {
        flex-direction: column-reverse;
    }
    .blog .container .groupA {
        margin: 2rem 0;
    }
    .blog .container .groupA .blogContainerArticle {
        width: 48%;   
    }
    .blog .container .groupA .blogContainerArticle .image {
    height: 250px;
    }    
}
@media (max-width: 700px) {
    .blog .container .groupB{
        grid-template-columns: repeat(1, 1fr);
    }
    .blog .container .groupA .blogContainerArticle {
        width: 100%;
    }
    .blog .heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}
@media (max-width: 550px) {
    .blog .heading h1 {
        font-size: 34px;
        line-height: 42px;
    }    
}