.cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem;
    margin: 4rem;
    background: var(--gradient-bar);
    border-radius: 0.7rem;

}
.cta .content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;
    color: #0e0e0e;
}
.cta .content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;
    color: #0e0e0e;
}
.cta .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}
.cta .btn button {
    background-color: #000000;
    border-radius: 40px;
    color: white;
    font-size: 18px;
    line-height: 30px;
    font-family: var(--font-family);
    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}
@media (max-width: 650px) {
    .cta {
        flex-direction: column;
    }
    .cta .btn {
        margin: 2rem 0 0;
    }
}
@media (max-width: 550px) {
    .cta {
        margin: 4rem 2rem;
    }
    .cta .content h3 {
        font-size: 18px;
        line-height: 32px;
    }
    .cta .btn button {
        font-size: 14px;
        line-height: 28px;
    }
}