:root {
  --font-family: 'Manrope', sans-serif;
  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 100%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.header {
    display: flex;
}
.header .content {
    display: flex;
    flex:1;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-right: 5rem;    
}
.header .content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}
.header .content p{
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
    margin-top: 1.5rem;
}
.header .content .input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}
.header .content .input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    border-radius: 5px 0px 0px 5px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.header .content .input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-size: 20px;
    font-weight: bold;
    line-height: 27px;
    background: #FF4820;
    border: 2px solid var(--color-footer);
    padding: 0 1rem;
    outline: none;
    color: #fff;
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}
.header .content .people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
.header .content .people img {
    width: 182px;
    height: 38px;
}
.header .content .people p {
    margin: 0 0 0 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    text-align: center;
}
.header .image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center; 
}
.header .image img {
    width: 100%;
    height: 100%;
}
/* Media Queries */
@media (max-width: 1050px) {
    .header{
        flex-direction: column;
    }
    .header .content {
        margin: 0 0 3rem;
    }
}
@media (max-width: 700px) {
    .header .content h1{
        font-size: 48px;
        line-height: 60px;
    }
    .header .content p {
        font-size: 16px;
        line-height: 24px;
    }
    .header .content .input input,
    .header .content .input button {
        font-size: 16px;
        line-height: 24px;
    }
}
@media (max-width: 490px) {
    .header .content h1{
        font-size: 30px;
        line-height: 48px;
    }
    .header .content p {
        font-size: 14px;
        line-height: 24px;
    }
    .header .content .input input,
    .header .content .input button {
        font-size: 12px;
        line-height: 16px;
    }
    .header .content .people {
        display: flex;
        flex-direction: column;
    }
}