:root {
  --font-family: 'Manrope', sans-serif;
  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.gpt3Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.gpt3Navbar .links {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.gpt3Navbar .links .logo {
    margin-right: 2rem;
}
.gpt3Navbar .links .logo img {
    width: 62px;
    height: 16px;
}
.gpt3Navbar .links .container{
    display: flex;
}
/*hover */
.gpt3Navbar .links .container a::after {
    content: "";
    display: block;
    margin: auto;
    height: 3px;
    width: 0;
    top: 5px;
    background: transparent;
    transition: all 0.3s;
}
.gpt3Navbar .links .container a:hover::after{
  width: 100%;
  background: var(--gradient-bar);
}
/*hover */
.gpt3Navbar .sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.gpt3Navbar .links .container p,
.gpt3Navbar .sign p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform:capitalize;
    cursor: pointer;
    margin: 0 1rem;
}
.gpt3Navbar button{
    padding: 0.5rem 1rem;
    width: 9em;
    height: 3em;
    border-radius: 30em;
    font-size: 15px;
    font-family: inherit;
    border: none;
    position: relative;
    overflow: hidden;
    z-index: 1;
    cursor: pointer;
}

.gpt3Navbar button::before{
    content: '';
    width: 0;
    height: 3em;
    border-radius: 30em;
    position: absolute;
    top: 0;
    left: 0;
    background-image: linear-gradient(to right, #22c1c3 0%, #fdbb2d 100%);
    transition: .5s ease;
    display: block;
    z-index: -1;
    cursor: pointer;
}

.gpt3Navbar button:hover::before {
    width: 9em;
    cursor: pointer;
}
.gpt3Navbar .menu {
    display: none;
    margin-left: 1rem;
    position: relative;
}
.gpt3Navbar .menu svg {
    cursor: pointer;
}
.gpt3Navbar .menu .container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0,0,0,0.2);
}
.gpt3Navbar .menu .container p {
    margin: 1rem 0;
}
.gpt3Navbar .menu .container .links .sign{
    display: none;
}
@media (max-width:1092px) {
    .gpt3Navbar .links .container{
        display: none;
    }
    .gpt3Navbar .menu {
        display: flex;
    }
    .gpt3Navbar .menu .container .links {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        color: #fff;
    }
}
@media (max-width:700px) {
    .gpt3Navbar{
        padding: 2rem 4rem;
    }
    .gpt3Navbar .menu {
        display: flex;
    }
    .gpt3Navbar .menu .container .links {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        color: #fff;
    }
    .gpt3Navbar .menu .container a::after {
    content: "";
    display: block;
    margin: auto;
    height: 3px;
    width: 0;
    top: 5px;
    background: transparent;
    transition: all 0.3s;
}
.gpt3Navbar .menu .container a:hover::after{
  width: 100%;
  background: var(--gradient-bar);
}
}
@media (max-width:550px) {
    .gpt3Navbar{
        padding: 2rem;
    }
    .gpt3Navbar .sign {
        display: none;
    }
    .gpt3Navbar .menu .container {
        top: 10px;
    }
    .gpt3Navbar .menu .container .links {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        color: #fff;
    }
    .gpt3Navbar .menu .container .links .sign {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    .gpt3Navbar .menu .container a::after {
    content: "";
    display: block;
    margin: auto;
    height: 3px;
    width: 0;
    top: 5px;
    background: transparent;
    transition: all 0.3s;
}
.gpt3Navbar .menu .container a:hover::after{
    width: 100%;
    background: var(--gradient-bar);
}
}