.blogContainerArticle {
    width: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}
.blogContainerArticle .image {
    height: 100%;
    width: 100%;
    background: var(--color-footer);
}
.blogContainerArticle .image img {
    height: 100%;
    width: 100%;
}
.blogContainerArticle .content {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 1rem 1.5rem;
    height: 100%;
}
.blogContainerArticle .content p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: bold;
    line-height: 35px;
    color: white;
}
.blogContainerArticle .content h3 {
    font-family: var(--font-family);
    font-size: 25px;
    font-weight: 800;
    line-height: 30px;
    color: white;
    margin-bottom: 5rem;
    cursor: pointer;
}
.blogContainerArticle .content p:last-child {
    cursor: pointer;
}
@media (max-width:550px) {
    .blogContainerArticle .content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}